<template>
  <div class="systems--parent-component">
    <h1>Computer Systems</h1>
    <systems-list v-if="!isLoading" :systems="computedSystems">
      <template slot-scope="system">
        <div class="systems-item">
          <h3>{{ system.name }}</h3>
          <p>{{ system.desc }}</p>
          <p>BUY NOW</p>
        </div>
      </template>
    </systems-list>
  </div>
</template>

<script>
import SystemsList from "./SystemsList"
import { RepositoryFactory } from "./../repositories/RepositoryFactory"

const SystemsRepository = RepositoryFactory.get("systems")

export default {
  name: "parent-component",
  components: { SystemsList },
  data() {
    return {
      isLoading: false,
      systems: []
    }
  },
  created() {
    this.fetch()
  },
  methods: {
    async fetch() {
      this.isLoading = true
      const { data } = await SystemsRepository.get()
      this.isLoading = false
      this.systems = data
    }
  },
  computed: {
    computedSystems() {
      return this.systems
    }
  }
}
</script>

<style lang="scss">
h1 {
  font-weight: normal;
}
.systems--parent-component {
  padding-left: 20px;
}
.systems-item {
  background-color: #f4f4f4;
  border-radius: 20px;
  padding: 20px;
  margin-bottom: 20px;
  h3 {
    margin: 0;
  }
}
</style>
