<template>
  <div class="systems--list">
    <div v-for="system in systems" v-bind="{ system }" :key="system.id">
      <slot v-bind="system"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "systems-list",
  props: {
    systems: {
      type: Array,
      required: true
    }
  }
}
</script>
